import React, { Component } from "react";

const fpFix = (num) => {
  return Math.round(num * 100) / 100;
};

export class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      //notes
      hundreds: 0,
      fifties: 0,
      twenties: 0,
      tens: 0,
      totalNotes: 0,

      //coins
      twoEuro: 0,
      oneEuro: 0,
      fiftyCents: 0,
      twentyCents: 0,
      tenCents: 0,
      fiveCents: 0,
      twoCents: 0,
      oneCents: 0,
      totalCoins: 0,

      //loose coins state
      looseOneCent: 0,
      looseTwoCent: 0,
      looseFiveCent: 0,
      looseTenCent: 0,
      looseTwentyCent: 0,
      looseFiftyCent: 0,
      looseOneEuro: 0,
      looseTwoEuro: 0,
      totalLoose: 0,

      //total
      subtotal: 0,
    };
  }

  //setters for notes
  setHundreds = (e) => {
    this.setState({ hundreds: parseInt(e.target.value) * 100 });
  };

  setFifties = (e) => {
    this.setState({ fifties: parseInt(e.target.value) * 50 });
  };

  setTwenties = (e) => {
    this.setState({ twenties: parseInt(e.target.value) * 20 });
  };

  setTens = (e) => {
    this.setState({ tens: parseInt(e.target.value) * 10 });
  };
  //end setters for notes

  //setters for coins
  setTwo = (e) => {
    this.setState({ twoEuro: parseInt(e.target.value) * 50 });
  };
  setOne = (e) => {
    this.setState({ oneEuro: parseInt(e.target.value) * 25 });
  };
  setFifty = (e) => {
    this.setState({ fiftyCents: parseInt(e.target.value) * 25 });
  };
  setTwenty = (e) => {
    this.setState({ twentyCents: parseInt(e.target.value) * 10 });
  };
  setTen = (e) => {
    this.setState({ tenCents: parseInt(e.target.value) * 10 });
  };
  setFive = (e) => {
    this.setState({ fiveCents: parseInt(e.target.value) * 5 });
  };
  setTwoCent = (e) => {
    this.setState({ twoCents: parseInt(e.target.value) * 2 });
  };
  setOneCent = (e) => {
    this.setState({ oneCents: parseInt(e.target.value) * 1 });
  };
  //end setters for coins

  //setters for loose coins
  looseSetTwo = (e) => {
    this.setState({ looseTwoEuro: parseFloat(e.target.value) % 50 });
  };
  looseSetOne = (e) => {
    this.setState({ looseOneEuro: parseFloat(e.target.value) % 25 });
  };
  looseSetFifty = (e) => {
    this.setState({ looseFiftyCent: parseFloat(e.target.value) % 25 });
  };
  looseSetTwenty = (e) => {
    let amount = fpFix(e.target.value % 10);
    this.setState({ looseTwentyCent: amount });
  };
  looseSetTen = (e) => {
    let amount = fpFix(e.target.value % 10);
    this.setState({ looseTenCent: amount });
  };
  looseSetFive = (e) => {
    let amount = fpFix(e.target.value % 5);
    this.setState({ looseFiveCent: amount });
  };
  looseSetTwoCent = (e) => {
    let amount = fpFix(e.target.value % 2);
    this.setState({ looseTwoCent: amount });
  };
  looseSetOneCent = (e) => {
    let amount = fpFix(e.target.value % 1);
    this.setState({ looseOneCent: amount });
  };
  //end setters for loose coins

  onSubmit = (e) => {
    e.preventDefault();
    this.setState({
      totalNotes:
        this.state.hundreds +
        this.state.fifties +
        this.state.twenties +
        this.state.tens,
    });

    this.setState({
      totalCoins:
        this.state.twoEuro +
        this.state.oneEuro +
        this.state.fiftyCents +
        this.state.twentyCents +
        this.state.tenCents +
        this.state.fiveCents +
        this.state.twoCents +
        this.state.oneCents,
    });

    let amount = fpFix(
      this.state.looseTwoEuro +
        this.state.looseOneEuro +
        this.state.looseFiftyCent +
        this.state.looseTwentyCent +
        this.state.looseTenCent +
        this.state.looseFiveCent +
        this.state.looseTwoCent +
        this.state.looseOneCent
    );
    this.setState({
      totalLoose: amount,
    });
  };

  render() {
    return (
      <div style={{ textAlign: "center" }}>
        <form action="" onSubmit={this.onSubmit}>
          <h3>Notes:</h3>
          <p>
            <strong>Enter number of notes</strong>
          </p>
          <p>
            100 Euro <input type="text" onChange={this.setHundreds} />{" "}
            {this.state.hundreds > 0 ? this.state.hundreds : 0}
          </p>

          <p>
            50 Euro <input type="text" onChange={this.setFifties} />{" "}
            {this.state.fifties > 0 ? this.state.fifties : 0}
          </p>

          <p>
            20 Euro <input type="text" onChange={this.setTwenties} />{" "}
            {this.state.twenties > 0 ? this.state.twenties : 0}
          </p>

          <p>
            10 Euro <input type="text" onChange={this.setTens} />{" "}
            {this.state.tens > 0 ? this.state.tens : 0}
          </p>

          <h3>Coins:</h3>
          <p>
            <strong>Enter number of bags</strong>
          </p>
          <p>
            2 Euro <input type="text" onChange={this.setTwo} />{" "}
            {this.state.twoEuro > 0 ? this.state.twoEuro : 0}
          </p>
          <p>
            1 Euro <input type="text" onChange={this.setOne} />{" "}
            {this.state.oneEuro > 0 ? this.state.oneEuro : 0}
          </p>
          <p>
            50 Cents <input type="text" onChange={this.setFifty} />{" "}
            {this.state.fiftyCents > 0 ? this.state.fiftyCents : 0}
          </p>
          <p>
            20 Cents <input type="text" onChange={this.setTwenty} />{" "}
            {this.state.twentyCents > 0 ? this.state.twentyCents : 0}
          </p>
          <p>
            10 Cents <input type="text" onChange={this.setTen} />{" "}
            {this.state.tenCents > 0 ? this.state.tenCents : 0}
          </p>
          <p>
            5 Cents <input type="text" onChange={this.setFive} />{" "}
            {this.state.fiveCents > 0 ? this.state.fiveCents : 0}
          </p>
          <p>
            2 Cents <input type="text" onChange={this.setTwoCent} />{" "}
            {this.state.twoCents > 0 ? this.state.twoCents : 0}
          </p>
          <p>
            1 Cent <input type="text" onChange={this.setOneCent} />{" "}
            {this.state.oneCents > 0 ? this.state.oneCents : 0}
          </p>

          <h3>Loose Coins:</h3>
          <p>
            <strong>Enter amount:</strong>
          </p>

          {/* loose coins start */}
          <p>
            2 Euro <input type="text" onChange={this.looseSetTwo} />{" "}
            {this.state.looseTwoEuro > 0 ? this.state.looseTwoEuro : 0}
          </p>
          <p>
            1 Euro <input type="text" onChange={this.looseSetOne} />{" "}
            {this.state.looseOneEuro > 0 ? this.state.looseOneEuro : 0}
          </p>
          <p>
            50 Cents <input type="text" onChange={this.looseSetFifty} />{" "}
            {this.state.looseFiftyCent > 0 ? this.state.looseFiftyCent : 0}
          </p>
          <p>
            20 Cents <input type="text" onChange={this.looseSetTwenty} />{" "}
            {this.state.looseTwentyCent > 0 ? this.state.looseTwentyCent : 0}
          </p>
          <p>
            10 Cents <input type="text" onChange={this.looseSetTen} />{" "}
            {this.state.looseTenCent > 0 ? this.state.looseTenCent : 0}
          </p>
          <p>
            5 Cents <input type="text" onChange={this.looseSetFive} />{" "}
            {this.state.looseFiveCent > 0 ? this.state.looseFiveCent : 0}
          </p>
          <p>
            2 Cents <input type="text" onChange={this.looseSetTwoCent} />{" "}
            {this.state.looseTwoCent > 0 ? this.state.looseTwoCent : 0}
          </p>
          <p>
            1 Cent <input type="text" onChange={this.looseSetOneCent} />{" "}
            {this.state.looseOneCent > 0 ? this.state.looseOneCent : 0}
          </p>

          <button type="submit">Calculate</button>
        </form>

        <p>
          Total Notes = {this.state.totalNotes > 0 ? this.state.totalNotes : 0}
        </p>
        <p>
          Total Coins = {this.state.totalCoins > 0 ? this.state.totalCoins : 0}
        </p>
        <p>
          Total Loose Coins ={" "}
          {this.state.totalLoose > 0 ? this.state.totalLoose : 0}
        </p>
        <button
          onClick={() => {
            this.setState({
              subtotal:
                this.state.totalNotes +
                this.state.totalCoins +
                this.state.totalLoose,
            });
          }}
        >
          Calculate Total
        </button>
        <p>
          Total Counted = {this.state.subtotal > 0 ? this.state.subtotal : 0}
        </p>
      </div>
    );
  }
}

export default App;
